.os-host-overflow-y{
	padding-right: 15px !important;
}
.os-host-overflow-x{
	padding-bottom: 15px !important;
}
.os-theme-dark > .os-scrollbar > .os-scrollbar-track > .os-scrollbar-handle{
	background: #626262 !important;
	opacity: 1;
	z-index: 10;
}
.os-host-overflow-y{
	.os-scrollbar-vertical{
		padding-top: 0;
		padding-bottom: 0;
		&:after{
			content: '';
			position: absolute;
			bottom: 0;
			top: 0;
			left: 50%;
			width: 1px;
			height: 100%;
			background: #d88e2e;
		}
	}
}
.os-host-overflow-x{
	.os-scrollbar-horizontal{
		padding-top: 0;
		padding-bottom: 0;
		height: 6px;
		&:after{
			content: '';
			position: absolute;
			bottom: 0;
			top: 50%;
			left: 50%;
			transform:translateX(-50%);
			width: calc(100% - 4px);
			height: 1px;
			background: #d88e2e;
		}
	}
}
